/* Variables */

:root {
  --background-color: #1e1e28;
  --navbar-color: #20202a;
  --text-color: #ffffff;
  --primary-color: #ffc107;
  --secondary-color: #8c8c8e;
  --cards-color: #2c2c38;

  --font-family-primary: "Bakbak One", cursive;
  --font-family-secondary: "Noto Sans Display", sans-serif;
}

/* Global properties */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: var(--background-color);
  font-family: var(--font-family-secondary);
  font-weight: 400;
}

/* Profile */

.profile {
  background-color: var(--background-color);
  color: 000000;
  box-shadow: 10px 0 5px -13px var(--secondary-color);
  width: 60%;
  height: 100vh;

  position: fixed;
  z-index: 10;
  visibility: hidden;
}

.profile__picture {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 30px;
  background-color: var(--cards-color);
}

.profile__picture img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

.profile__picture p:nth-child(2) {
  color: var(--text-color);
  font-weight: 600;

  margin-top: 12px;
}

.profile__picture p:nth-child(3) {
  color: var(--secondary-color);
  font-size: 0.9rem;
  margin-top: 4px;
}

.profile__picture p:nth-child(4) {
  color: var(--secondary-color);
  font-size: 0.9rem;
  margin-bottom: 24px;
}

.title h2 {
  text-align: left;
  margin: 24px 18px;
  font-size: 0.9rem;
}

.languages {
  display: flex;
  justify-content: space-evenly;
  color: var(--text-color);
  font-size: 0.8rem;
  margin: 18px;
  padding-bottom: 24px;
  border-bottom: 1px solid var(--secondary-color);
}

.languages p {
  border: 1px solid var(--text-color);
  border-radius: 10px;
  padding: 4px;
}

.languages p:hover {
  background-color: gray;
}

.coding {
  display: flex;
  flex-direction: column;
  color: var(--text-color);
  font-size: 0.9rem;
  margin: 18px;
  padding-bottom: 24px;
  border-bottom: 1px solid var(--secondary-color);
}

.coding p {
  border-radius: 8px;
  padding: 8px 12px;
  margin-bottom: 12px;
}

.coding p:nth-child(1) {
  background-color: #dd4b25;
}

.coding p:nth-child(2) {
  background-color: #016ab5;
}

.coding p:nth-child(3) {
  background-color: #d5ba32;
}

.coding p:hover {
  opacity: 80%;
}

.knowledge {
  display: flex;
  flex-direction: column;
  color: var(--secondary-color);
  font-size: 0.9rem;
  margin: 18px;
  padding-bottom: 24px;
  border-bottom: 1px solid var(--secondary-color);
}

.knowledge p {
  margin: 2px;
  font-size: 0.8rem;
}

.fa-check-circle {
  color: var(--primary-color);
  margin-left: 18px;
  margin-right: 8px;
}

.profile__footer {
  display: flex;
  justify-content: center;
}
.profile__footer .fab {
  color: var(--secondary-color);
  font-size: 1.4rem;
}

/* Navbar */

.navbar {
  background-color: var(--navbar-color);
  color: var(--secondary-color);

  height: 70px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 30px;

  font-size: 1.4rem;
}

.fa-ellipsis-v {
  z-index: 10;
}

.fa-ellipsis-v:hover,
.fa-bars:hover {
  color: var(--text-color);
  transition: 0.4s;
  cursor: pointer;
}

/* Heading section */

.heading__background {
  height: 500px;
  background-image: linear-gradient(
      180deg,
      transparent,
      rgba(30, 30, 40, 1) 50%
    ),
    url(https://images.pexels.com/photos/3244513/pexels-photo-3244513.jpeg?auto=compress&cs=tinysrgb&h=650&w=940);

  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.heading__background img {
  width: 100%;
  object-fit: cover;
}

.heading__container {
  background-image: linear-gradient(180deg, transparent, rgba(30, 30, 40, 0.7)),
    url(https://images.pexels.com/photos/3244513/pexels-photo-3244513.jpeg?auto=compress&cs=tinysrgb&h=650&w=940);

  width: 90%;
  height: 85%;

  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: absolute;
}

h1 {
  font-family: var(--font-family-primary);
  font-size: 4rem;
  color: var(--text-color);
}

code {
  color: var(--text-color);
  font-size: 0.7rem;
}

code::before {
  content: "<code> ";
  color: var(--primary-color);
}

code::after {
  content: " <code>";
  color: var(--primary-color);
}

button {
  background-color: var(--primary-color);

  width: 160px;
  height: 44px;

  border: none;
  margin-top: 60px;
  margin-bottom: 20px;

  font-family: var(--font-family-secondary);
  font-weight: 600;
  font-size: 0.8rem;
  letter-spacing: 0.2em;

  cursor: pointer;
}

button:hover {
  transform: scale(1.1);
  transition: 0.8s;
}

/* Profile Links */

.links__container {
  display: flex;
  justify-content: space-evenly;
  padding-bottom: 40px;
  margin: 0 10px;
}

.fa-linkedin,
.fa-github {
  margin: 0 16px;
  font-size: 2rem;
  color: var(--primary-color);
}

.links a {
  display: flex;
  align-items: center;
  font-size: 0.9rem;
  font-weight: 600;
  text-decoration: none;
  color: var(--text-color);
}

.links a:hover {
  color: var(--secondary-color);
  transition: 0.2s;
}

/* Info Cards */

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card {
  background-color: var(--cards-color);
  margin: 12px 24px;
  padding: 24px;
}

h2 {
  font-size: 1.1rem;
  color: var(--text-color);
  text-align: center;
}

h3 {
  font-size: 0.9rem;
  color: var(--text-color);
  margin-bottom: 8px;
}

.card p {
  font-size: 0.9rem;
  line-height: 24px;
  color: var(--secondary-color);
  margin-bottom: 16px;
}

.card .order {
  font-family: var(--font-family-primary);
  font-size: 0.9rem;
  color: var(--primary-color);
}

.card .order:hover {
  margin-right: 4px;
  transition: 0.6s;
}

.fa-chevron-circle-right {
  font-size: 0.7rem;
  color: var(--primary-color);
}

/* Footer */

.footer__container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 16px 26px;

  background-color: var(--cards-color);
  color: var(--secondary-color);
  font-size: 0.8rem;
  text-decoration: none;
}

.email a {
  color: var(--secondary-color);
  font-size: 0.8rem;
  text-decoration: none;
}

.email a:hover {
  color: var(--text-color);
}
